@media (max-width: 768px){

    
    .icons {
        display: inline-flex;
    }

    #check:checked~.icons #menu-icon {
        display: none;
    }

    .icons #close-icon {
        display: none;
    }

    #check:checked~.icons #close-icon {
        display: block;
    }

    .navbar {
        position: absolute;
        top: 100%;
        left: 0;
        width: 100%;
        height: 0;
        background-color: rgba(0, 250, 33, 0.212);
        -webkit-backdrop-filter: blur(3px);
        backdrop-filter: blur(3px);
        box-shadow: 0 .5rem 1rem rgba(0, 0, 0, .1);
        overflow: hidden;
        transition: .3s ease;
    }

    .navbar a {
        font-family: 'Poppins', sans-serif;
        font-size: 700;

    }

    .navbar a:hover {
        color: #89fc0e;
    }

    .home-btn {
        color: #0d6d0d;
    }
    
    .recipes-btn {
        color: #258d2a;
    }
    
    .about-us-btn {
        color: #1f9e13;
    }
    
    .contact-btn {
        color: #3cb400;
    }
    
    #menu-icon {
        color: #0e750e;

    }

    #close-icon {
        color: #0e750e;
    }

    #check:checked~.navbar {
        height: 17.7rem;
    }

    .navbar a{
        display: block;
        font-size: 1.1rem;
        margin: 1.5rem 0;
        text-align: center;
        transform: translateY(-50px);
        opacity: 0;
        transition: .3s ease;
    }

    #check:checked~.navbar a {
        transform: translateY(0);
        opacity: 1;
        transition-delay: calc(.15s * var(--i));
    }

    .slideshow {
       margin: 5rem;
    }

    .welcome-text {
        position: absolute;
        margin-top: 65rem;
        left: 80%;
        transform: translate(-50%, -50%);
        width: 100%;
        z-index: 1;
    }

    .welcome-the-most-message {
        position: absolute;
        transform: translate(-50%, -50%);
        left: 115%;
        width: 100%;
    }

    .Chicken {
        margin-right:  45rem;
    }

    .Pork {
        margin-right: 8rem;
    }

    .Beef {
        margin-right: 45rem;
    }

    .Goat {
        margin-right: 8rem;
    }

    .Frog {
        margin-right: 45rem;
    }

    .Egg {
        margin-right: 8rem;
    }

    .Fish {
        margin-right: 45rem;
    }

    .Squid {
        margin-right: 8rem;
    }


    
    

}

